<template>
  <input
    v-model.trim="inputModel"
    @keypress.enter="emitData"
    type="search"
    id="searchinput"
    :placeholder="placeholder"
  >
</template>

<script>
export default {
  props: ['placeholder'],
  emits: ['emitSearchInput'],
  data () {
    return {
      inputModel: ''
    }
  },
  methods: {
    emitData () {
      this.$emit('emitSearchInput', this.inputModel)
      this.inputModel = ''
    }
  }
}
</script>

<style scoped>
  input {
    border-radius: 10px;
    border: 1px solid #E4E4E4;

    padding: 12px 16px;
    margin-bottom: 5px;
    font-size: 16px;
    width: 380px;

    background-image: url('../assets/searchinput/zoom.svg');
    background-repeat: no-repeat;
    background-position: right 16px top 50%;
  }
  input::placeholder {
    color: #293148;
    opacity: 80%;
  }
  input:focus{
    border: 1px solid #7895FF;
    outline: 2px solid #C9D5FF;
    background: none;
  }
  /* removes cross */
  input[type="search"]::-webkit-search-decoration,
  input[type="search"]::-webkit-search-cancel-button,
  input[type="search"]::-webkit-search-results-button,
  input[type="search"]::-webkit-search-results-decoration {
    -webkit-appearance:none;
  }
</style>
