<template>
  <div>
    <div class="employees_header">
      <span class="employees_title">Employees</span>
      <PersonPannel></PersonPannel>
    </div>
    <div class="employees_pannel">
      <SearchInput :placeholder="'Search'"></SearchInput>
      <button class="add_new_empl_btn" @click="this.$router.push('/dashboard/employee-create')"><img src="../../assets/employeesdachboard/plus.svg">Add New Employee</button>
    </div>
    <table class="employees_table">
      <thead>
        <tr>
          <td>ID</td>
          <td>Username</td>
          <td>E-mail</td>
          <td>Phone</td>
          <td>Credits</td>
          <td>Status</td>
        </tr>
      </thead>
      <tbody>
        <tr v-for="person in showTwelveItems(pageIdx)" :key="person.id">
          <td>{{ person.id }}</td>
          <td>
            <router-link :to="{ name: 'employee-profile', params: { id: person.id }}">{{ person.username }}</router-link>
          </td>
          <td>{{ person.email }}</td>
          <td>{{ person.phone }}</td>
          <td>{{ person.credits }}</td>
          <td><div class="table_status" :class="person.status" @click="toggleStatus(person)"><span>{{ person.status }}</span></div></td>
        </tr>
      </tbody>
    </table>
    <div class="employees_footer">
      <span class="employees_footer__showing_out">Showing {{ showExactNumOfItems }} out of {{ employeesData.length }}</span>
      <div class="employees_footer__nav">
        <a href="#" class="nav_left" @click.prevent="moveTableLeft"></a>
        <span class="nav_num_box"><span class="nav_num">{{ pageCounter }}</span>of<span class="nav_num">{{ pageExactCounter }}</span></span>
        <a href="#" class="nav_right" @click.prevent="moveTableRight"></a>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

import PersonPannel from '@/components/PersonPannel.vue'
import SearchInput from '@/components/SearchInput.vue'
export default {
  data () {
    return {
      pageIdx: 0,
      pageCounter: 1
    }
  },
  computed: {
    ...mapState(['employeesData']),
    pageExactCounter () {
      return Math.ceil(this.employeesData.length / 12)
    },
    showExactNumOfItems () {
      if ((this.pageIdx + 12) < this.employeesData.length) {
        return this.pageIdx + 12
      } else {
        return this.employeesData.length
      }
    }
  },
  methods: {
    showTwelveItems (idx) {
      if (idx === 0) {
        return this.employeesData.slice(idx, 12)
      } else {
        return this.employeesData.slice(idx, 12 + idx)
      }
    },
    moveTableRight () {
      if (this.pageIdx < this.employeesData.length - 12) {
        this.pageIdx += 12
        this.pageCounter++
      }
    },
    moveTableLeft () {
      if (this.pageIdx !== 0) {
        this.pageIdx -= 12
        this.pageCounter--
      }
    },
    toggleStatus (arg) {
      if (arg.status === 'active') {
        arg.status = 'blocked'
      } else {
        arg.status = 'active'
      }
    }
  },
  components: {
    PersonPannel,
    SearchInput
  }
}
</script>
